export const GET_STOPWATCH_NAME_TASK = 'GET_STOPWATCH_NAME_TASK';
export const GET_STOPWATCH_TIME_TO_START = 'GET_STOPWATCH_TIME_TO_START';
export const GET_STOPWATCH_TIME_TO_END = 'GET_STOPWATCH_TIME_TO_END';
export const GET_STOPWATCH_TIME_TO_SPEND = 'GET_OPWATCH_TIME_TO_SPEND';

export const getNameStopwatch = name => ({
  type: GET_STOPWATCH_NAME_TASK,
  payload: name
})

export const getTimeToStartStopwatch = timeToStart => ({
  type: GET_STOPWATCH_TIME_TO_START,
  payload: timeToStart
})

export const getTimeToStopStopwatch = timeToStop => ({
  type: GET_STOPWATCH_TIME_TO_END,
  payload: timeToStop
})

export const getTimeToSpendStopwatch = timeToSpend => ({
  type: GET_STOPWATCH_TIME_TO_SPEND,
  payload: timeToSpend
})
